import React, { useContext } from "react";
import "./contact.css";
import { LanguageContext } from "../../Contexts/LanguageContext";
import "bootstrap-icons/font/bootstrap-icons.css";
import { AnimatedOnScroll } from "react-animated-css-onscroll";

const Contact = () => {
    const { language, setLanguage } = useContext(LanguageContext);

    return (
        <div className="contactDiv">
            <AnimatedOnScroll
                animationIn="fadeInLeft"
                style={{
                    order: language === "en" ? "1" : "2",
                    direction: language === "en" ? "ltr" : "rtl",
                }}
            >
                <h1>
                    {language === "en" ? "This is your life." : "  ."}
                    <br />
                    {language === "en" ? "Come alive for it." : " ."}
                </h1>
                <p>
                    {language === "en"
                        ? "Were a vital fitness network: with everything you need to ignite, maintain, and enhance your fitness journey. And we're here to help. Have questions about membership or billing? Want to become an affiliate? Or are you a trainer looking for a great place to work? Reach out."
                        : "   :       ,      .   .     ?   ?         ?  ."}
                </p>
                <p>
                    {language === "en"
                        ? "Email us directly."
                        : "   ."}{" "}
                    <br />
                    proactive.test@gmail.com
                </p>
                <p>
                    {language === "en" ? "Schedule a call." : " ."}{" "}
                    <br />
                    050-1234567
                </p>
            </AnimatedOnScroll>
            <AnimatedOnScroll
                animationIn="fadeInRight"
                style={{
                    order: language === "en" ? "2" : "1",
                    direction: language === "en" ? "ltr" : "rtl",
                }}
            >
                <form>
                    <table>
                        <tbody>
                            <tr>
                                <td>
                                    <input
                                        type="text"
                                        placeholder={
                                            language === "en"
                                                ? "First Name*"
                                                : " *"
                                        }
                                        required
                                    />
                                </td>
                                <td>
                                    <input
                                        type="text"
                                        placeholder={
                                            language === "en"
                                                ? "Last Name*"
                                                : " *"
                                        }
                                        required
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="email"
                                        placeholder={
                                            language === "en"
                                                ? "Email*"
                                                : "*"
                                        }
                                        required
                                    />
                                </td>
                                <td>
                                    <input
                                        type="phone"
                                        placeholder={
                                            language === "en"
                                                ? "Phone*"
                                                : "*"
                                        }
                                        required
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <textarea
                                        placeholder={
                                            language === "en"
                                                ? "Message"
                                                : ""
                                        }
                                    />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <button>
                        <span className="text">
                            {language === "en"
                                ? "SEND MESSAGE  "
                                : "   "}
                            <i className="bi bi-arrow-up-right"></i>
                        </span>
                        <span>
                            {language === "en" ? "SEND MESSAGE" : " "}
                            <i className="bi bi-arrow-up-right"></i>
                        </span>
                    </button>
                </form>
            </AnimatedOnScroll>
        </div>
    );
};

export default Contact;
